// @flow
import React from 'react';
import type { Element, AbstractComponent } from 'react';
import _isEmpty from 'lodash/isEmpty';
import _get from 'lodash/get';
import { Item } from './Elements';
import { ConvertDTPropertyAddress } from 'shared/Consts';
import { hideIfInvalidProps } from 'utils/RenderComponent';

type Props = {
  forms: Object,
  property: Object,
  propertyIndex: number,
};

const checkValidProps = ({ forms, property }) =>
  !_isEmpty(forms) && !_isEmpty(property);

const Property = (props: Props): Element<'div'> => {
  const { property, propertyIndex, forms } = props;
  const { PropertyId: propertyId, Property: propertyData } = property;

  const orderForms = forms.sort(
    (formA, formB) => _get(formA, ['Order'], 0) - _get(formB, ['Order'], 0),
  );

  return (
    <div className="offer_block">
      <div className="offer_container offer_title">
        <div className="offer_no">
          Property {propertyIndex + 1} :{' '}
          {ConvertDTPropertyAddress(propertyData)}
        </div>
        <div className="offer_item" />
      </div>
      <div className="offer_item">
        {orderForms.map(form => (
          <Item
            form={form}
            propertyId={propertyId}
            key={_get(form, ['FormId'])}
          />
        ))}
      </div>
    </div>
  );
};

export default (hideIfInvalidProps(checkValidProps)(
  Property,
): AbstractComponent<{}>);
