// @flow
import * as React from 'react';
import { APP_BASE_URL } from 'shared/Consts';
import { FORM_STATUS } from 'components/FormBuilder/Constants';
import { CheckCircle } from './CheckCircle';
import { FileText } from './FileText';

type Props = {
  propertyId: number,
  form: Object,
};

export const Item = (props: Props): React.Element<'div'> => {
  const { propertyId, form } = props;
  const { FormId: formId, Name: name, DataStatus: formStatus } = form;

  return (
    <div className="offer_item_action">
      {formStatus === FORM_STATUS.submitted ? <CheckCircle /> : <FileText />}
      <a
        className="offer_item_link"
        href={`${APP_BASE_URL}/property?propertyId=${propertyId}&formId=${formId}`}
      >
        {name}
      </a>
    </div>
  );
};
